.arrow {
  height: 100%;
  width: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.arrowTag {
  position: absolute;
  height: 5%;
  width: 5%;
  top: 93%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.arrowTag:hover {
  height: 7%;
  width: 7%;
  -webkit-animation: bump .3s ease;
          animation: bump .3s ease; }

@-webkit-keyframes bump {
  0% {
    top: 93%; }
  50% {
    top: 91.5%; }
  100% {
    top: 93%; } }

@keyframes bump {
  0% {
    top: 93%; }
  50% {
    top: 91.5%; }
  100% {
    top: 93%; } }

.App-header {
