.facebook {
  padding: .5em;
  border: 1px solid #ccc;
  display: block;
  margin: -1px 0 0 -1px;
  color: black;
  background: -webkit-gradient(linear, right top, left top, color-stop(50%, #333), color-stop(#333), color-stop(#4a6ea9), color-stop(50%, #4a6ea9));
  background: linear-gradient(to left, #333 50%, #333, #4a6ea9, #4a6ea9 50%);
  background-size: 202% 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-animation: down-bump 0.4s ease;
          animation: down-bump 0.4s ease; }

.instagram {
  padding: 1em;
  border: 1px solid #ccc;
  display: block;
  margin: -1px 0 0 -1px;
  color: black;
  background: -webkit-gradient(linear, right top, left top, color-stop(50%, #333), color-stop(#333), color-stop(hotpink), color-stop(50%, hotpink));
  background: linear-gradient(to left, #333 50%, #333, hotpink, hotpink 50%);
  background-size: 202% 100%;
